import React from 'react'
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import AOS from 'aos';
import 'aos/dist/aos.css';

AOS.init();

export const Achievements = () => {
  return (
    <div id='achievements'>
        <div className="achievements">
        <h1 data-aos="fade-down" data-aos-duration="1000">Achievements</h1>
        <br />
        <div className="ach-row">
          <div className="ach-img" data-aos="fade-right" data-aos-duration="900"><img className="ach-img" src="../img/re-cloud.png" alt="" /></div>
          <div className="ach-img" data-aos="fade-left" data-aos-duration="900"><img className="ach-img" src="../img/ecs.png" alt="" /></div>
        </div>
      </div>
    </div>
  )
}
